export default {
	name: 'tabview',
	props: {
		list: {
			type: Array
		},
		gtype: {
			type: String,
			default: '0'
		}
	},
	data() {
		return {};
	},
	methods: {
		tpinfo(e) {
			let id = e.currentTarget.dataset.id;
			uni.navigateTo({
				url: '/pages/goods/goodsInfo/goodsInfo?gid=' + id
			});
		}
	}
};
